import axios from "axios";
const baseURL = 'https://simple-twitter-0901-98d00e19f6cc.herokuapp.com/api'

//  instance
const axiosInstance = axios.create({
  baseURL: baseURL,
})

// axiosInstance  interceptors 
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem("authToken");
    if (token) {
      config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    console.error(error);
  }
);

// tweets
// GET: /tweets
export const getAllTweets = async () => {
  try {
    const res = await axiosInstance.get(`${baseURL}/tweets`)
    // console.log("getAllTweets", res.data)
    return res.data
  } catch (error) {
    console.error("[Get AllTweets Failed]: ", error.response.data.message)
  }
}

// 
// /tweets/:tweet_id
export const getTweet = async (id) => {
  try {
    const res = await axiosInstance.get(`${baseURL}/tweets/${id}`)
    return res.data
  } catch (error) {
    console.error("[Get Tweet Failed]: ", error.response.data.message)
  }
}

