import './Setting.scss'
import Swal from 'sweetalert2';
import { useState, useEffect, useRef } from 'react'

// API
import { putUserData } from 'api/users';

// 
const emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;

export const Setting = () => {
  // id
  const { id, introduction } = JSON.parse(localStorage.getItem("userInfo"))

  //  input 
  const [account, setAccount] = useState('')
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [passwordCheck, setPasswordCheck] = useState('')

  // useRef
  const accountInputRef = useRef(null)
  const nameInputRef = useRef(null)
  const emailInputRef = useRef(null)
  const passwordRef = useRef(null)
  const checkPasswordRef = useRef(null)

  // 
  const accountLength = account.length
  const nameLength = name.length
  const accountLimit = 20
  const nameLimit = 50

  // 
  const [accountIsValid, setAccountIsValid] = useState(false)
  const [nameIsValid, setNameIsValid] = useState(false)
  const [emailIsValid, setEmailIsValid] = useState(false)
  const [passwordIsValid, setPasswordIsValid] = useState(false)
  const [passwordCheckIsValid, setPasswordCheckIsValid] = useState(false)

  // 
  const [accountAlert, setAccountAlert] = useState(false)
  const [nameAlert, setNameAlert] = useState(false)
  const [emailAlert, setEmailAlert] = useState(false)

  // input
  function checkAccount(inputValue) {
    // 20
    if(inputValue.trim().length > accountLimit) {
      return setAccountAlert(true)
    } else {
      setAccountAlert(false)
      setAccountIsValid(true)
    }
  }
  function checkName(inputValue) {
    // 50
    if(inputValue.trim().length > nameLimit) {
      setNameAlert(true)
    } else {
      setNameAlert(false)
      setNameIsValid(true)
    }
  }
  function checkEmail(inputValue) {
    // emailRule
    if(!emailRule.test(inputValue)) {
      setEmailAlert(true)
    } else {
      setEmailAlert(false)
      setEmailIsValid(true)
    }
  }

  useEffect(() => {
    account ? setAccountIsValid(true) : setAccountIsValid(false)
    name ? setNameIsValid(true) : setNameIsValid(false)
    email ? setEmailIsValid(true) : setEmailIsValid(false)
    password ? setPasswordIsValid(true) : setPasswordCheckIsValid(false)
    passwordCheck ? setPasswordCheckIsValid(true) : setPasswordCheckIsValid(false)
  })

  // 
  const onFormSubmit = async () => {
    // 
    if(!accountIsValid || !nameIsValid || !emailIsValid || !passwordIsValid || !passwordCheckIsValid) {
      return
    }

    // 
    if(password !== passwordCheck) {
      Swal.fire("")
      return
    }

    const res = await putUserData({id, account, name, email, password, checkPassword: passwordCheck, introduction})
    // console.log(res)

    // 
    Swal.fire("")

    // 
    setAccount('')
    setName('')
    setEmail('')
    setPassword('')
    setPasswordCheck('')
  }

  return (
    <div className="settingContainer">
      <div className="topSection">
        <p className="title"></p>
      </div>
      <div className="bottomSection">
        <form className="settingForm">
          <div className="input">
            <div className="inputLabel"></div>
            <input 
              name="account" 
              className="inputBox" 
              type="text" 
              onChange={(e) => {
                checkAccount(e.target.value)
                setAccount(e.target.value)
              }}
              value={account}
            />
            {accountAlert && <div className='validationAlert'>20</div>}
            <div className="wordCount">
              {accountLength}/{accountLimit}
            </div>
          </div>
          <div className="input">
            <div className="inputLabel"></div>
            <input 
              name="name" 
              className="inputBox" 
              type="text" 
              onChange={(e) => {
                checkName(e.target.value)
                setName(e.target.value)
              }}
              value={name}
            />
            {nameAlert && <div className='validationAlert'>50</div>}
            <div className="wordCount">
              {nameLength}/{nameLimit}
            </div>
          </div>
          <div className="input">
            <div className="inputLabel">Email</div>
            <input 
              name="email" 
              className="inputBox" 
              type="text" 
              onChange={(e) =>{
                checkEmail(e.target.value)
                setEmail(e.target.value)
              }}
              value={email}
            />
            {emailAlert && <div className='validationAlert'></div>}
          </div>
          <div className="input">
            <div className="inputLabel"></div>
            <input 
              name="password" 
              className="inputBox" 
              type="password" 
              onChange={(e) => setPassword(e.target.value)}
              value={password}
            />
          </div>
          <div className="input">
            <div className="inputLabel"></div>
            <input 
              name="checkPassword" 
              className="inputBox" 
              type="password" 
              onChange={(e) => {
                setPasswordCheck(e.target.value)
              }}
              value={passwordCheck}
            />
          </div>
        </form>
        <div className="buttonBox">
          {/*  */}
          <button 
            className={accountIsValid & nameIsValid & emailIsValid & passwordIsValid & passwordCheckIsValid ? 'valid' : 'invalid'}
            onClick={(e) => onFormSubmit(e)}
          >
            
          </button>
        </div>
      </div>
    </div>
  )
}