import { login, adminLogin } from 'api/auth';
import { createContext, useContext, useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import jwt_decode from 'jwt-decode'


const defaultAuthContext = {
  isAuthenticated: false,
  currentMember: null,
  register: null,
  login: null,
  adminLogin: null,
  logout: null,
};

export const AuthContext = createContext(defaultAuthContext);
export default function AuthContextProvider({ children }) {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [currentUser, setCurrentUser] = useState(null)
  const [payload, setPayload] = useState(null)

  // userInfo accountnameid 
  const [userInfo, setUserInfo] = useState({})
  // tweetId
  const [tweetId, setTweetId] = useState(null)
  //  tweet user account
  const [userTweetAccount, setUserTweetAccount] = useState('')
  //  tweet
  const [userReplyList, setUserReplyList] = useState([]);
  // Tweet
  const [isTweetUpdated, setIsTweetUpdated] = useState(false)
  // 
  const [isUserEdited, setIsUserEdited] = useState(false)
  // 
  const [isUpdateLikes, setIsUpdateLikes] = useState(false)
  // 
  const [isUpdatedReplies, setIsUpdatedReplies] = useState(false) 

  const { pathname } = useLocation();
  const navigate = useNavigate()

  function handleSetTweetIdClick(tweetIdReceived) {
    setTweetId(tweetIdReceived)
    localStorage.setItem("tweetId", tweetIdReceived)
  }

  //  tweet user account
  function handleSetUserTweetAccount(accountReceived) {
    setUserTweetAccount(accountReceived);
    localStorage.setItem("userTweetAccount", accountReceived);
  };

  //  token
  useEffect(() => {
    // 
    if(pathname === '/login' || pathname === '/signup' || pathname === '/admin-login') return

    //TweetisTweetUpdatedfalse
    setIsTweetUpdated(false)
    //isUserEditedfalse
    setIsUserEdited(false)
    //isUpdatedRepliesfalse
    setIsUpdatedReplies(false)
    //isUpdateLikesfalse
    setIsUpdateLikes(false)

    // 
    const checkTokenIsValid = async () => {
      // localStoragetoken
      const authToken = localStorage.getItem("authToken")

      // token
      if(!authToken) {
        setIsAuthenticated(false)
        setPayload(null)
        return navigate('/login')
      }

      // token
      if(authToken) {
        // authToken
        const tempPayload = jwt_decode(authToken)
        setPayload(tempPayload)

        // payload
        if(!tempPayload) {
          setIsAuthenticated(false)
          setPayload(null)
          return navigate('/login')
        }

        setIsAuthenticated(true)
        
        // localStorageuserInfo
        const savedUserInfo = localStorage.getItem("userInfo")
        if(savedUserInfo) {
          setUserInfo(JSON.parse(savedUserInfo))
        }

        // localStoragetweetId
        const savedTweetId = localStorage.getItem("tweetId");
        if (savedTweetId) {
          setTweetId(savedTweetId);
        }

      } else {
        // 
        setIsAuthenticated(false)
        setPayload(null)
        navigate('/login')
      }
    }

    // 
    checkTokenIsValid()
    // console.log('AuthProvider re-rendered.')

  // 
  }, [pathname, navigate])

  return (
    <AuthContext.Provider 
      value={{
        isAuthenticated,
        setIsAuthenticated,
        currentUser,
        userInfo,
        tweetId,
        handleSetTweetIdClick,
        handleSetUserTweetAccount,
        userReplyList,
        setUserReplyList,
        isTweetUpdated,
        setIsTweetUpdated,
        isUserEdited,
        setIsUserEdited,
        isUpdatedReplies,
        setIsUpdatedReplies,
        isUpdateLikes,
        setIsUpdateLikes,

        login: async (data) => {
          const response = await login({
            account: data.account,
            password: data.password
          })

          // payload
          if(response.data) {
            if(response.data.status === "success") {
              const authToken = response.data.token
              // 
              const tempPayload = jwt_decode(authToken)
              setPayload(tempPayload)
              setIsAuthenticated(true)
              setCurrentUser({
                id: tempPayload.id
              })
              // token
              localStorage.setItem("authToken", authToken)

              // 
              // setUserInfo(JSON.stringify(response.data.user))
              localStorage.setItem("userInfo", JSON.stringify(response.data.user))
            }
            // responseresponseLoginPage
          } else {
            setPayload(null)
            setIsAuthenticated(false)
          }
          
          return response
        },
        adminLogin: async (data) => {
          const response = await adminLogin({
            account: data.account,
            password: data.password
          })
          
          // console.log('adminLogin: ', response)

          if (response) {
            if (response.status === "success") {
              const authToken = response.token
              // 
              const tempPayload = jwt_decode(authToken)
              setPayload(tempPayload)
              setIsAuthenticated(true)
              setCurrentUser({
                id: tempPayload.id
              })
              // token
              localStorage.setItem("authToken", authToken)

              // 
              setUserInfo(response.user)
              localStorage.setItem("userInfo", JSON.stringify(response.user))
            }
            // responseresponseLoginPage
          } else {
            setPayload(null)
            setIsAuthenticated(false)
          }
          return response
        }
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}
