import './Modal.scss'

// import dependencies
import Swal from 'sweetalert2'
import { useNavigate } from 'react-router-dom'

// import icons
import { ReactComponent as CloseIcon } from 'assets/icons/close.svg'
import { useState, useEffect } from 'react'

// import api
import { postTweet } from 'api/tweets'

export const Modal = ({ setShowModal }) => {
  const navigate = useNavigate()

  // 
  const { avatar } = JSON.parse(localStorage.getItem("userInfo"))

  // textarea
  const [content, setContent] = useState('')

  // 
  const contentLength = content.length
  const contentLimit = 160

  // 
  const [contentIsValid, setContentIsValid] = useState(false)

  // 
  const [alert, setAlert] = useState(false)

  // textarea
  function checkContent(inputValue) {
    // 160
    if(inputValue.trim().length > contentLimit) {
      return setAlert(true)
    } else {
      setAlert(false)
      setContentIsValid(true)
    }
  }

  // 
  const onSubmit = async (e) => {
    e.preventDefault()

    // 
    if(!contentIsValid) return

    // 
    await postTweet({ description: content })
  
    // 
    Swal.fire({
      position: 'top',
      title: '',
      timer: 1000,
      icon: 'success',
      showConfirmButton: false,
    });

    // modalcontent
    setShowModal(false)
    setContent('')
    navigate('/main')
  }

  useEffect(() => {
    content ? setContentIsValid(true) : setContentIsValid(false)
  })

  return (
    <div className="postModalBox">
      <div className="topBar">
        <div className="iconBox" onClick={() => {
          // 
          if(content) {
            Swal.fire({
              title: "",
              text: "",
              icon: "warning",
              showCancelButton: true,
              confirmButtonText: "",
              confirmButtonColor: "#FF6600",
              cancelButtonText: ""
            }).then((result) => {

              // 
              if(result.isConfirmed) {
                setShowModal(false)
                navigate('/main')
              } 
            })

            // 
          } else {
            setShowModal(false)
            navigate('/main')
          }
        }}>
          <CloseIcon className='closeIcon'/>
        </div>
      </div>
      <div className="modalAvatarBox">
        <img className="avatar" src={avatar} alt="avatar" />
      </div>
      <textarea 
        name="tweet" 
        className="addTweetContent" 
        type="text" 
        placeholder="" 
        onChange={(e) => {
          checkContent(e.target.value)
          setContent(e.target.value)
        }}
      >
      </textarea>
      {alert && <div className='alert'>160</div>}
      <div className="wordCount">
        {contentLength}/{contentLimit}
      </div>
      <button 
        className={contentIsValid ? 'submitValid' : 'submitInvalid'}
        onClick={(e) => onSubmit(e)}
      >
        
      </button>
    </div>
  )
}