import './AdminLogin.scss'
import Swal from 'sweetalert2'
import { useState, useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom'
import { AuthInput } from 'components/SignUp/SignUp'
import { TopIcon } from 'components/SignUp/SignUp'
import { OrangeBtn } from 'components/SignUp/SignUp'
// import { AdminLogin } from 'api/auth'
import { AuthContext } from 'context/AuthContext'

export const AdminLogin = () => {
  const [account, setAccount] = useState('')
  const [password, setPassword] = useState('')
  const navigate = useNavigate()
  const { adminLogin, setIsAuthenticated } = useContext(AuthContext)

  const handleSubmit = (event) => {
    event.preventDefault()

  }


  // 
  const handleClick = async () => {
    // 
    if (account.trim().length === 0 || password.trim().length === 0) return
    const response = await adminLogin({ account, password })

    // 
    if (!response) {
      if (response.status !== "success")
        setIsAuthenticated(false)
      // 
      Swal.fire({
        position: 'top',
        title: '',
        timer: 1000,
        icon: 'error',
        showConfirmButton: false,
      });
      return
    }

    // 
    // 
    Swal.fire({
      position: 'top',
      title: '',
      timer: 1000,
      icon: 'success',
      showConfirmButton: false,
    });
    setIsAuthenticated(true)
    navigate('/admin_main')
  }


  return (
    <div className='adminLoginContainer'>
      <TopIcon title="" />

      {/* .map */}
      <form onSubmit={handleSubmit}>
        <AuthInput
          id="account"
          type="text"
          label=""
          value={account}
          placeholder=""
          onChange={(accountInputValue) => setAccount(accountInputValue)}
          valuelength={account.length}
          textlength="20"
        />
        <AuthInput
          id="password"
          name="password"
          type="password"
          label=""
          value={password}
          placeholder=""
          onChange={(passwordInputValue) => setPassword(passwordInputValue)}
          valuelength={password.length}
          textlength="50"
        />
      </form>
      <div className='btnGroup'>
        <OrangeBtn
          way=""
          onClick={handleClick}
        />
        <div className='aLink'>
          <Link to="http://localhost:3000/login"></Link>

          {/* <a href="#" className='back-signup-link' onClick={handleLogin}></a> */}
        </div>
      </div>
    </div>
  )
}